import { AppBar, IconButton, makeStyles, Toolbar } from "@material-ui/core";
import CallEndIcon from "@material-ui/icons/CallEnd";
import MicIcon from "@material-ui/icons/Mic";
import MicOffIcon from "@material-ui/icons/MicOff";
import VideocamIcon from "@material-ui/icons/Videocam";
import VideocamOffIcon from "@material-ui/icons/VideocamOff";
import Peer from "peerjs";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import socketIOClient from "socket.io-client";
import useAudio from "../../components/Utils";
const ENDPOINT = "https://call2friend.herokuapp.com";

const useStyles = makeStyles((theme) => ({
  appBar: {
    top: "auto",
    bottom: 0,
  },
  grow: {
    float: "right",
    position: "absolute",
    right: "30px",
  },
  controlBtn: {
    margin: "5px",
  },
  callEndBtn: {
    margin: "5px",
    backgroundColor: "red",
  },
}));

export default function DailCall() {
  const socket = socketIOClient(ENDPOINT);
  const [playing, toggle] = useAudio("telephone_ring.mp3");
  const params = useParams();
  const history = useHistory();
  const callId = params.id;
  const classes = useStyles();
  const [stream, setStream] = useState();
  const [audio, setAudio] = useState(true);
  const [control, setControl] = useState(false);
  const [video, setVideo] = useState(true);
  const [userId, setUserID] = useState(0);
  let videoGrid;
  const user = JSON.parse(localStorage.getItem("user"));
  const peer = new Peer(user._id, {
    port: "443",
    secure: true,
    host: "school-peer.herokuapp.com",
  });

  useEffect(() => {
    videoGrid = document.getElementById("video-grid");
    callUser(user._id, callId);
    loadMedia();
    play();
  }, [userId]);

  useEffect(() => {
    return () => {
      console.log("End");
    };
  }, []);

  function switchVideo(val) {
    setVideo(val);
    stream.getVideoTracks()[0].enabled = val;
  }

  function switchAudio(val) {
    setAudio(val);
    stream.getAudioTracks()[0].enabled = val;
  }

  function callEnd() {
    stream.getTracks().forEach(function (track) {
      track.stop();
    });
    history.push("/app/contacts");
  }
  function play() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }

  function callUser(from, to) {
    fetch(
      "http://teachit-api.herokuapp.com/teachit/api/v1/user/" + to + "/" + from,
      {
        method: "get",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      }
    )
      .then((Response) => Response.json())
      .then((response) => {
        if (response) {
          console.log(response);
        } else {
          console.log("unable to inser");
        }
      });
  }

  function loadMedia() {
    const myVideo = document.createElement("video");
    myVideo.muted = true;
    navigator.mediaDevices
      .getUserMedia({
        video: true,
        audio: true,
      })
      .then((stream) => {
        debugger;
        setStream(stream);
        addVideoStream(myVideo, stream);
        peer.on("call", function (call) {
          console.log("Received the call from client");
          call.answer(stream);
          call.on("stream", function (userStream) {
            console.log("Add the client stream");
            if (!document.getElementById(callId)) {
              const myVideo = document.createElement("video");
              myVideo.setAttribute("id", callId);
              addVideoStream(myVideo, userStream);
            }
          });
        });
      });
  }

  function addVideoStream(video, stream) {
    video.srcObject = stream;
    video.addEventListener("loadedmetadata", () => {
      video.play();
    });
    videoGrid.append(video);
  }

  return (
    <div>
      <div id="video-grid"></div>
      <AppBar position="fixed" color="primary" className={classes.appBar}>
        <Toolbar>
          <div className={classes.grow}>
            <IconButton
              color="inherit"
              onClick={() => switchVideo(!video)}
              edge="end"
              className={classes.controlBtn}
            >
              {video ? <VideocamIcon /> : <VideocamOffIcon />}
            </IconButton>
            <IconButton
              edge="end"
              color="inherit"
              onClick={() => switchAudio(!audio)}
              className={classes.controlBtn}
            >
              {audio ? <MicIcon /> : <MicOffIcon />}
            </IconButton>
            <IconButton
              color="inherit"
              onClick={() => callEnd()}
              edge="end"
              className={classes.callEndBtn}
            >
              <CallEndIcon />
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
      <audio className="audio-element">
        <source src="sample.mp3"></source>
      </audio>
    </div>
  );
}
